import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';

@Component({
    selector: 'app-using-documents',
    templateUrl: './using-documents.component.html',
    styleUrls: ['./using-documents.component.scss']
})
export class UsingDocumentsComponent implements OnInit {
    paramsList: Array<any> = [];

    constructor(
        public location: Location
    ) { }

    ngOnInit() {
        this.paramsList = [
            {
                name: 'options',
                explain: '',
                type: 'Array<{text:String,id:String,disabled?:Boolean}>',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'type',
                explain: 'btn,icon',
                type: 'String',
                optionaValue: 'btn,icon',
                defaultValue: 'btn'
            },
            {
                name: 'position',
                explain: '',
                type: 'String',
                optionaValue: 'center,right,left',
                defaultValue: 'center'
            },
            {
                name: 'dropText',
                explain: 'btntitleicon',
                type: 'String',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'icon',
                explain: 'icondropdown',
                type: 'String',
                optionaValue: '',
                defaultValue: 'la la-ellipsis-h'
            },
            {
                name: 'iconClass',
                explain: 'iconicon',
                type: 'String',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'btnColor',
                explain: 'btn',
                type: 'String',
                optionaValue: '',
                defaultValue: 'btn-secondary'
            },
            {
                name: 'separateCharacter',
                explain: 'btn',
                type: 'String',
                optionaValue: '',
                defaultValue: '/'
            },
            {
                name: 'trigger',
                explain: 'dropdown',
                type: 'String',
                optionaValue: 'click,hover',
                defaultValue: 'click'
            },
            {
                name: 'selectParent',
                explain: '',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'false'
            },
            {
                name: 'multiselect',
                explain: 'btn',
                type: 'Boolean',
                optionaValue: 'true,false',
                defaultValue: 'false'
            },
            {
                name: 'selected',
                explain: 'itemitem',
                type: '',
                optionaValue: '',
                defaultValue: '-'
            },
            {
                name: 'remove',
                explain: 'btnvalue',
                type: '',
                optionaValue: '',
                defaultValue: '-'
            }
        ];
    };
};



